.container{
    list-style: none;
    text-decoration: none;
    background-color: #7d9263;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    margin: 30px;
    }

.container-name{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1.5rem;
    color: rgb(2, 58, 7);
}

.container-short{
    color:black;
    padding: 10px;
    text-align: left;
}

a{
    text-decoration: none;
}

h1, h2{
    color: #7d9263;
}

span{
    color:red;
}